.navbar {
    width: 100%;
    height: 80px;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    color: white;
    transition: 1s ease;


    &.scroll-effect{
        background: black;
        transition: 1s ease;
    }


    .main-navbar {
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;

        .left-navbar {
            width: 70%;
            height: 100%;
            // border: 1px solid yellowgreen;
            display: flex;
            gap: 20px;
            justify-content: flex-start;
            align-items: center;

            .audio-btn{
                font-size: 15px;
                color: white;
                background: transparent;
                border: none;
                outline: none;
            }

            .logo {
                margin-left: 50px;
                font-family: "Samarkan";
                color: var(--secondary-color);
                letter-spacing: 3px;
                transition: scale 0.5s ease;
                cursor: pointer;
                user-select: none;

                &::selection {
                    color: none;
                    background: none;
                }


                &:hover {
                    scale: 1.2;
                    transition: scale 0.5s ease;
                }

            }

            .menu {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                gap: 80px;
                list-style: none;
                padding-left: 30px;
                font-family: sans-serif;

                li {
                    a {
                        color: white;
                        text-decoration: none;
                        font-family: 'Mulish', sans-serif;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        padding: 3px 10px;
                        position: relative;

                        

                        &:hover{
                            animation: glow 0.5s ease-in-out infinite alternate;
                        }

                    }
                }
            }
        }

        .right-navbar {
            width: 30%;
            height: 100%;
            display: flex;
            justify-content: flex-end;
            align-items: center;

            .side-bar-btn {
                margin-right: 20px;
                background: transparent;
                color: white;
                outline: none;
                border: none;
                font-size: 20px;
                display: none;
                justify-content: center;
                align-items: center;
            }

            a {
                margin-right: 40px;
                text-decoration: none;
                color: black;
                display: inline-block;
                overflow: hidden;
                color: var(--secondary-color);
                font-family: 'Mulish', sans-serif;
                position: relative;
                z-index: 1;
                

                &:hover {
                    color: #f5a403;
                    &::before {
                        transform: translate(0, 0);
                        transition-delay: 0s;
                    }

                    &::after {
                        transform: translate(0, 0);
                        transition-delay: .3s;
                        
                    }

                    span {
                        &::before {
                            transform: translate(0, 0);
                            transition-delay: .6s;
                        }

                        &::after {
                            transform: translate(0, 0);
                            transition-delay: .9s;
                        }

                    }
                }

                &::before {
                    content: "";
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    background: #f5a403;
                    transition: transform .3s ease;
                    width: 100%;
                    height: 2px;
                    transform: translateX(-100%);
                    transition-delay: .9s;

                }

                &::after {
                    content: "";
                    position: absolute;
                    right: 0;
                    bottom: 0;
                    background: #f5a403;
                    transition: transform .3s ease;
                    width: 2px;
                    height: 100%;
                    transform: translateY(100%);
                    transition-delay: .6s;

                }

                span {
                    position: relative;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    gap: 3px;
                    overflow: hidden;
                    padding: 10px;

                    &::before {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 1px;
                        width: 100%;
                        height: 2px;
                        background: #f5a403;
                        transform: translateX(100%);
                        transition: transform .3s ease;
                        transition-delay: .3s;
                    }

                    &::after {
                        content: '';
                        position: absolute;
                        top: 0;
                        left: 0;
                        width: 2px;
                        height: 100%;
                        background: #f5a403;
                        transition: transform .3s ease;
                        transform: translateY(-100%);
                        transition-delay: 0s;
                    }

                }



            }
        }

    }

    // SIDE BAR 

    .side-bar {
        width: 50%;
        padding: 25px;
        position: absolute;
        top: 0;
        right: -100%;
        // transparancy
        // background: rgba(255, 255, 255, 0.249);
        // box-shadow: 0 8px 32px 0 rgba(31, 38, 135, 0.37);
        // backdrop-filter: blur(5px);
        // -webkit-backdrop-filter: blur(5px);
        // border: 1px solid rgba(255, 255, 255, 0.18);
        background: linear-gradient(-239.59deg, #2c2c2d -44.65%, #1a1a1e 75.57%);
        transition: 0.5s ease-in-out;
        border-bottom-left-radius: 24px;
        overflow: hidden;
        z-index: 2;

        &::after{
            content: '';
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            right: 0px;
            background: linear-gradient(-239.59deg, #af9d3b -44.65%, #1a1a1e 75.57%);
            z-index: 1;



        }

        &.opened {
            right: 0;
            transition: 0.5s ease-in-out;
        }


        .side-menu {
            list-style: none;
            display: flex;
            justify-content: flex-start;
            align-items: flex-start;
            flex-direction: column;
            gap: 20px;
            position: relative;
            z-index: 100;


            li {
                width: 100%;
                height: 35px;
                font-size: 20px;
                display: flex;
                justify-content: flex-end;
                align-items: center;

                .side-bar-btn {
                    margin-right: 20px;
                    background: transparent;
                    color: white;
                    outline: none;
                    border: none;
                    font-size: 20px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                }

                a {
                    width: 100%;
                    height: 100%;
                    color: white;
                    text-decoration: none;
                    font-weight: bold;
                    font-family: sans-serif;
                    translate: 110% 0 0;

                    &.anim1 {
                        transition: translate 1s ease;
                        translate: 0 0 0;
                    }

                    &.anim2 {
                        transition: translate 1.2s ease;
                        translate: 0;
                    }

                    &.anim3 {
                        transition: translate 1.4s ease;
                        translate: 0;
                    }

                    &.anim4 {
                        transition: translate 1.6s ease;
                        translate: 0;
                    }

                    &.anim5 {
                        transition: translate 1.7s ease;
                        translate: 0;
                    }

                }
            }
        }

    }
}

// ANIMATION FOR SIDE BAR

@keyframes rtl {
    0% {
        translate: 110% 0 0;
        opacity: 0;
    }

    100% {
        translate: 0 0 0;
    }

}


// FOR REPONSIVE

@media screen and (max-width:990px) {

    .navbar {
        .main-navbar {
            .left-navbar {
                .menu {
                    display: none;
                }

                .logo {
                    margin-left: 20px;
                    font-size: 15px;
                }

            }

            .right-navbar {
                .side-bar-btn {
                    display: flex;
                }

                a {
                    display: none;
                }
            }
        }
    }

}