.Eventpage {
    width: 100%;
    // height: 100vh;
    overflow: hidden;
    scroll-behavior: smooth;

    .eventpage-wrap {
        width: 100%;
        height: 100%;
        overflow-x: hidden;
        overflow-y: auto;
        scroll-behavior: smooth;

        .event-banner {
            width: 100%;
            height: 84vh;
            overflow: hidden;
        }


        .eventnav {
            width: 100%;
            border-bottom: 1px solid;
            border-color: var(--secondary-color);

            // background-color: rgb(235, 229, 229);
            .event-navmenu {
                height: 100%;
                padding-left: 30px;
                // border: 1px solid;
                display: flex;
                list-style: none;
                justify-content: flex-start;
                gap: 80px;
                align-items: center;
                margin: 15px;

                li {
                    padding: 7px;
                    // border: 1px solid rgb(249, 246, 246);
                    border-radius: 2px;

                    &:hover {
                        // background-color:var(--secondary-color)
                        border: 1px solid white;
                    }

                    a {
                        text-decoration: none;
                        color: rgb(239, 234, 234);
                        font-size: 20px;
                        font-weight: 700;

                        // &:hover{
                        //     // color: rgb(19, 9, 9);
                        // }
                        .diff-font {
                            color: var(--secondary-color);
                            font-family: "samarkan";
                        }
                    }

                }
            }
        }

        .event-detail {
            width: 100%;
            display: flex;
            justify-content: center;
            align-items: flex-start;
            padding: 50px 0;

            .event-wrapdetail {
                width: 95%;
                display: flex;
                flex-direction: column;
                justify-content: flex-start;
                align-items: flex-start;
                gap: 200px;
                // border: 2px solid white;

                .event-row{
                    width: 100%;
                    // border: 2px solid white;

                    h1 {
                        color: var(--secondary-color);
                        font-family: "samarkan";
                        font-size: 5vw;
                        letter-spacing: 3px;
                        margin-top: 20px;
                    }

                    h2 {
                        color: aliceblue;
                        font-size: 2vw;
                        font-family: "Mulish";
                        margin: 20px 0;
                    }

                    .row-details {
                        width: 100%;

                        p {
                            color: aliceblue;
                            text-align: left;
                            font-size: 20px;
                            font-family: var(--secondary-font);
                        }
                    }

                    .row-events {
                        width: 100%;

                    }
                }

            }

        }
    }


}


@media screen and (max-width:991px) {
    .Eventpage{
        .eventpage-wrap{
            .event-banner{
                height: 100vh;
            }
        }
    }
}